.Nav_Menu {
  height: 60px;
  background-color: #800000;
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
}
.Hamburger_Button {
  position: absolute;
  top: 20%;
  width: 20%;
  align-self: flex-end;
}

.dropdown_content {
  position: relative;
  align-self: center;
  margin-top: 50px;
  width: 100%;
  z-index: 1;
}
.dropdown_content li {
  text-align: center;
  width: 100%;
  color: black;
  padding: 20px 16px;
  text-decoration: none;
  display: block;
  background-color: #8b0000;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.24);
  color: white;
  font-family: "Playfair Display", serif;
  font-weight: bold;
  font-size: 1.5em;
}
